	clear: left;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-top: 1em;
	padding-left: 0;
}

.main-navigation ul ul {
	display: none;
	top: 1.5em;
	z-index: 99999;
}

.main-navigation ul ul.toggled-on {
	display: block;
}

.main-navigation ul ul ul {
	top: 0;
}

.main-navigation ul ul li {
	padding-left: 1em;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
	left: 100%;
}

.main-navigation ul ul a {
	width: 200px;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}

.main-navigation li {
	position: relative;
}

.main-navigation a {
	display: inline-block;
	width: 100%;
	padding: .5em 1em .5em 0;
	text-decoration: none;
	color: #fff;
}

.main-navigation a:hover,
.main-navigation a:focus {
	text-decoration: underline;
}

.main-navigation .menu-item-has-children,
.main-navigation .page_item_has_children {
	min-width: 218px;
}

.main-navigation .menu-item-has-children > a,
.main-navigation .page_item_has_children > a {
	padding-right: 2em;
}

button.dropdown-toggle {
	position: absolute;
	right: 0;
	border: none;
	background: inherit;
	color: white;
	line-height: 1.5em;
	padding: .4em 1em .4em .5em;
}

.menu-toggle {
	position: absolute;
	top: 0;
	right: 0;
	display: block;
	margin: 1.2em 1.2em 0 0;
	padding: .6em .8em;
	font-size: 80%;
	text-transform: uppercase;
	color: white;
	border: 1px solid rgba(255, 255, 255, 0.3);
}

/* Toggle small menu and children on */
.toggled-on ul,
.sub-menu.toggled-on {
	display: block;
}

@media screen and (min-width: 600px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation .menu-item-has-children > a,
	.main-navigation .page_item_has_children > a {
		padding-right: 2em;
		background: rgba(255, 255, 255, 0.1);
	}
	.main-navigation ul {
		display: block;
		display: flex;
		flex-wrap: wrap;
	}
	.main-navigation ul ul {
		flex-direction: column;
		background: rgba(255, 255, 255, 0.1);
		margin-left: 0;
	}
	.main-navigation ul ul li {
		padding-left: 0;
	}
	.main-navigation ul ul li a {
		width: 218px;
		background: none;
	}
	.main-navigation ul li a {
		padding: .4em 1em;
	}
}

@media screen and (min-width: 900px) {
	.main-navigation ul {
		justify-content: flex-end;
		padding-top: 0;
		/* If you want dropdowns to open on hover, uncomment this: */
		/*
      li:hover > ul,
          li:focus > ul {
          display: block;
      }
      */
	}
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.entry-header {
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
}

.cat-links {
	font-size: 90%;
	font-weight: 700;
}

.cat-links a {
	text-decoration: none;
	text-transform: uppercase;
	color: #a70d22;
}

.cat-links a:hover, .cat-links a:focus {
	background-color: #a70d22;
	color: white;
}

.entry-title {
	margin: .125em 0 .25em;
	font-size: 2.5em;
}

@media screen and (min-width: 900px) {
	.entry-title {
		font-size: 3em;
	}
}

.entry-title a {
	text-decoration: none;
	color: black;
}

.entry-title a:hover, .entry-title a:focus {
	border-bottom: 5px solid #a70d22;
}

.entry-meta {
	font-size: 90%;
}

.entry-meta a {
	font-weight: 700;
	text-decoration: none;
	color: black;
}

.entry-meta a:hover, .entry-meta a:focus {
	color: black;
	border-bottom: 3px solid #a70d22;
}

.byline::after {
	content: '|';
	margin: 0 .5em;
}

.comments-link::before,
.edit-link::before {
	content: '|';
	margin: 0 .5em;
}

.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.extra {
	display: none;
}

/* Custom Post Meta rules for single post / no sidebar. */
.no-sidebar .entry-meta {
	margin-top: 1.5em;
}

@media screen and (min-width: 900px) {
	.no-sidebar .post-content__wrap .entry-meta {
		margin-top: 2.25em;
		font-size: 80%;
	}
	.no-sidebar .post-content__wrap .entry-meta a {
		font-size: 110%;
		display: block;
		border-bottom: 3px solid white;
	}
	.no-sidebar .post-content__wrap .entry-meta a:hover, .no-sidebar .post-content__wrap .entry-meta a:focus {
		border-bottom-color: #a70d22;
	}
	.no-sidebar .post-content__wrap .byline,
	.no-sidebar .post-content__wrap .posted-on,
	.no-sidebar .post-content__wrap .comments-link,
	.no-sidebar .post-content__wrap .edit-link {
		display: block;
		margin-bottom: 1em;
	}
	.no-sidebar .post-content__wrap .byline:before, .no-sidebar .post-content__wrap .byline:after,
	.no-sidebar .post-content__wrap .posted-on:before,
	.no-sidebar .post-content__wrap .posted-on:after,
	.no-sidebar .post-content__wrap .comments-link:before,
	.no-sidebar .post-content__wrap .comments-link:after,
	.no-sidebar .post-content__wrap .edit-link:before,
	.no-sidebar .post-content__wrap .edit-link:after {
		margin: 0;
		content: "";
	}
	.no-sidebar .post-content__wrap .extra {
		display: inline;
	}
}

.full-bleed {
	width: 100vw;
	max-height: 1200px;
	margin-top: 1em;
	margin-left: -1em;
	border-top: 1px solid #e6e6e6;
	border-bottom: 1px solid #e6e6e6;
	overflow: hidden;
}

@media screen and (min-width: 600px) {
	.full-bleed {
		margin-left: -2em;
	}
}

@media screen and (min-width: 900px) {
	.full-bleed {
		margin-top: 1.5em;
		margin-right: -2em;
	}
}

@media screen and (min-width: 1080px) {
	.full-bleed {
		margin-right: calc( ((-100vw + 1080px)/2) - 2em);
		margin-left: calc( ((-100vw + 1080px)/2) - 2em);
	}
}

.full-bleed img {
	display: block;
	margin: 0 auto;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.entry-footer {
	margin: 1.5em 0;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comments-area {
	width: 100%;
	margin: 3em 0;
	padding: 1.5em 0;
	border-top: 1px solid #cccccc;
}

.comments-title {
	margin-bottom: 2em;
	font-size: 2.5em;
	font-weight: 900;
}

.comment-list {
	padding: 0;
	margin: 0;
	list-style-type: none;
}

.comment-list ol {
	list-style-type: none;
}

.comment-body {
	border-bottom: 1px solid #c3c3c3;
	margin-bottom: 3em;
}

.comment-meta {
	position: relative;
	margin-left: 70px;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	font-size: 80%;
}

.comment-meta a,
.comment-form a {
	text-decoration: none;
	border: none;
}

.comment-meta a:hover, .comment-meta a:focus,
.comment-form a:hover,
.comment-form a:focus {
	text-decoration: underline;
}

.comment .avatar {
	position: absolute;
	top: -5px;
	left: -70px;
	width: 50px;
	height: 50px;
	border-radius: 50px;
}

.children {
	margin-left: 1em;
	padding-left: 0;
}

@media screen and (min-width: 900px) {
	.children {
		margin-left: 2em;
	}
}

@media screen and (min-width: 900px) {
	.no-sidebar .children {
		margin-left: 2em;
	}
	.no-sidebar .children .comment-meta {
		margin-left: 80px;
		font-size: 90%;
	}
	.no-sidebar .children .comment .avatar {
		top: -7px;
		left: -80px;
		width: 60px;
		height: 60px;
	}
	.no-sidebar .children .comment-content {
		margin-left: 80px;
	}
}

@media screen and (min-width: 1080px) {
	.no-sidebar .children {
		margin-left: 3em;
	}
	.no-sidebar .comment-meta {
		margin-left: 90px;
	}
	.no-sidebar .comment .avatar {
		top: -8px;
		left: -90px;
		width: 70px;
		height: 70px;
	}
	.no-sidebar .comment-content {
		margin-left: 90px;
	}
}

.comment-metadata {
	margin-top: .2em;
	padding-bottom: .5em;
	font-size: .8em;
	line-height: 1em;
}

.comment-metadata a {
	color: #7D7D7D;
}

.comment-metadata a:hover, .comment-metadata a:focus {
	border-color: #c3c3c3;
}

.comment-author {
	font-size: 1.4em;
	line-height: 1.3em;
}

.says {
	font-size: 80%;
}

.reply {
	margin-bottom: 1.5em;
	text-align: right;
}

.reply a {
	display: inline-block;
	padding: .5em 1.4em;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	font-size: 85%;
	color: #000;
	line-height: 1.3em;
	text-decoration: none;
	border: 1px solid #c3c3c3;
}

.reply a:hover, .reply a:focus {
	color: #fff;
	background: #a70d22;
	border-color: #a70d22;
}

.comment-content ol {
	list-style-type: decimal;
}

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor .avatar {
	box-sizing: content-box;
	border: 5px solid #a70d22;
	margin-top: -5px;
	margin-left: -5px;
}

.comment-reply-title {
	margin-bottom: 1em;
}

.comment-list .comment-reply-title {
	text-align: left;
}

.comment-form {
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	line-height: 1.3em;
}

.comment-form a {
	font-weight: 500;
}

.comment-form .form-submit {
	text-align: right;
}

.comment-form .form-submit input {
	padding: .5em 1.4em;
	font-size: 85%;
	color: #000;
	line-height: 1.3em;
	text-decoration: none;
	border: 1px solid #c3c3c3;
}

.comment-form .form-submit input:hover, .comment-form .form-submit input:focus {
	color: #fff;
	background: #a70d22;
	border-color: #a70d22;
}

.comment-form label {
	width: 33%;
	display: block;
	font-style: italic;
	padding-bottom: .4em;
}

.comment-form .required {
	color: #D54E21;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
	position: relative;
	padding: 1em;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	color: #fff;
	background-color: #00295e;
	text-align: center;
}

.site-footer a {
	font-weight: 700;
	color: white;
	text-decoration: none;
}

.site-footer a:hover, .site-footer a:focus {
	text-decoration: underline;
}

@media screen and (min-width: 600px) {
	.site-footer {
		padding: 1em 2em;
	}
}

.site-footer__wrap {
	max-width: 1080px;
	margin: 0 auto;
}

@media screen and (min-width: 900px) {
	.site-footer__wrap {
		display: flex;
		justify-content: space-between;
	}
}

.site-info {
	margin: 1em 0 1em auto;
	align-self: center;
}

@media screen and (min-width: 900px) {
	.site-info {
		text-align: right;
	}
	.site-info span {
		display: block;
	}
}

.social-menu {
	margin: 1em 0;
}

.social-menu ul {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.social-menu ul a {
	display: block;
	padding: .5em 1em;
	color: white;
	text-decoration: none;
	opacity: .6;
}

.social-menu ul a:hover, .social-menu ul a:focus {
	text-decoration: underline;
	opacity: 1;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget-area {
	margin-top: 1.5em;
}

.widget {
	margin: 0 0 2em;
	padding: 0 0 2em;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
}

.widget ul,
.widget ol {
	padding: .7em 0 0;
	margin-bottom: 0;
	list-style-type: none;
}

.widget ul li,
.widget ol li {
	display: block;
	line-height: 2em;
}

.widget ul li ul,
.widget ul li ol,
.widget ol li ul,
.widget ol li ol {
	margin-left: .5em;
}

@media screen and (min-width: 30em) {
	.widget ul li ul,
	.widget ul li ol,
	.widget ol li ul,
	.widget ol li ol {
		margin-left: 1em;
	}
}

.widget a {
	font-weight: 600;
	text-decoration: none;
}

.widget a:hover, .widget a:focus {
	border-bottom: 3px solid #a70d22;
}

.widget select {
	width: 100%;
	height: 2.5em;
	padding: 10px;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	border: 1px solid #ccc;
}

.widget-title {
	margin-top: 0;
	margin-bottom: .5em;
	font-size: 1.4em;
}

.widget_archive li {
	color: #7D7D7D;
}

.widget_categories .children {
	padding-top: 0;
}

.widget_search .search-submit {
	display: none;
}

.widget_pages .children,
.widget_nav_menu .sub-menu {
	padding-top: 0;
	padding-bottom: .5em;
}

.widget_pages a,
.widget_nav_menu a {
	display: block;
	padding: 1em 0;
	text-decoration: none;
	border-bottom: 1px dashed gray;
}

.widget_pages a:hover, .widget_pages a:focus,
.widget_nav_menu a:hover,
.widget_nav_menu a:focus {
	text-decoration: none;
	border-bottom: 1px dashed gray;
}

.widget_pages li,
.widget_nav_menu li {
	padding: 0;
}

.widget_meta li {
	padding: 0;
}

.widget_recent_comments li {
	line-height: 1.3em;
	padding-bottom: .5em;
}

.widget_recent_comments a:hover,
.widget_recent_comments a:focus {
	padding-left: 0;
}

.widget_rss li {
	margin-bottom: 1em;
}

.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rss-date {
	display: table;
	line-height: 1.5em;
	border-bottom: 3px solid #a70d22;
}

.rssSummary {
	padding: .5em 0;
	font-family: "Roboto Slab", Georgia, Cambria, "Times New Roman", Times, serif;
	font-size: .8em;
	line-height: 1.4em;
}

.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

.widget_calendar {
	font-weight: 400;
}

.widget_calendar caption {
	padding: .5em 0;
	font-size: 1em;
	font-weight: 500;
	text-align: left;
}

.widget_calendar thead {
	background: transparent;
	color: black;
}

.widget_calendar thead th {
	border-bottom-width: 2px;
}

.widget_calendar td {
	padding: .2em;
	font-size: .8em;
	text-align: center;
	background: white;
	border: 1px solid #cccccc;
}

.widget_calendar th {
	border: none;
	text-align: center;
}

.widget_calendar .pad {
	background: #eaeaea;
}

.widget_calendar a {
	font-weight: 900;
	text-decoration: none;
}

.textwidget {
	font-size: 80%;
	line-height: 1.3em;
}

.footer-widgets {
	margin: 2em 0 0;
	padding: 1em 0;
	background-color: #1a1a1a;
}

@media screen and (min-width: 600px) {
	.footer-widgets {
		padding: 2em 1em;
	}
}

.footer-widgets .widget {
	flex: 1;
	margin: 0 1em 2em;
	padding-top: 1em;
}

.footer-widgets .widget .original-title {
	color: rgba(255, 255, 255, 0.5);
}

.footer-widgets .widget,
.footer-widgets .widget-title,
.footer-widgets .widget a,
.footer-widgets .widget_calendar thead,
.footer-widgets .rss-date,
.footer-widgets .widget_rss cite {
	color: white;
}

.footer-widgets .widget_calendar td,
.footer-widgets .widget_calendar a {
	color: black;
}

@media screen and (min-width: 900px) {
	.footer-widgets {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}
	.footer-widgets .widget:only-of-type {
		width: 100%;
	}
	.footer-widgets .widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2) {
		flex: 2;
	}
	.footer-widgets .widget:nth-last-child(n+4),
	.footer-widgets .widget:nth-last-child(n+4) ~ .widget {
		width: 30%;
		flex: 1 1 30%;
	}
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 2em;
	max-width: 100%;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	font-size: 90%;
	color: #666666;
	text-align: center;
}

.wp-caption a {
	color: inherit;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: .5em 0 1.5em 1em;
}

@media screen and (min-width: 600px) {
	.wp-caption .wp-caption-text {
		margin-left: 2em;
	}
}

@media screen and (min-width: 900px) {
	.wp-caption .wp-caption-text {
		margin-left: 0;
	}
}

/*--------------------------------------------------------------
## Images
--------------------------------------------------------------*/
.wp-caption.aligncenter,
.centered-image {
	max-width: 100vw;
	margin: 0 -1em;
}

@media screen and (min-width: 600px) {
	.wp-caption.aligncenter,
	.centered-image {
		margin: 0 -2em;
	}
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin: 1.5em 0;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
	padding: 1em;
}

.gallery-item img {
	display: block;
}

.gallery-item a {
	display: inline-block;
	border-bottom: none;
}

.gallery-item a:hover, .gallery-item a:focus {
	border-bottom: none;
	box-shadow: none;
}

.gallery-item a:hover img, .gallery-item a:focus img {
	border-color: #a70d22;
}

.gallery-item a img {
	padding: .5em;
	margin: 0 auto;
	border: 1px solid #fff;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
	margin: 0;
	font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", Geneva, Arial, sans-serif;
	border: none;
}

/*--------------------------------------------------------------
## Icons
--------------------------------------------------------------*/
.icon {
	display: inline-block;
	fill: currentColor;
	height: 1em;
	position: relative;
	top: -0.0625em;
	vertical-align: middle;
	width: 1em;
}

.svg-fallback {
	display: none;
}

.no-svg .svg-fallback {
	display: inline-block;
}

.no-svg .svg-fallback.icon-arrow-long-left:before {
	content: "←";
}

.no-svg .svg-fallback.icon-arrow-long-right:before {
	content: "→";
}

.no-svg .social-menu a {
	height: auto;
	width: auto;
}

.no-svg .social-menu li a .screen-reader-text {
	clip: auto;
	font-size: 16px;
	font-size: 1rem;
	font-weight: 400;
	height: auto;
	position: relative !important;
	width: auto;
}
